/**
 * 用户接口
 */
import http from '@/utils/http.js'
import { API_URL } from '@/utils/constants.js'
import Qs from 'qs'

/**
 * 角色列表
 * @param {*} params  查询参数
 */
export const roleList = params => {
  return http.get(API_URL.ROLE_LIST_URL, { params })
}

/**
 * 角色新增
 * @param {*} params
 */
export const roleNew = params => {
  return http.post(API_URL.ROLE_NEW_URL, Qs.stringify(params), {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  })
}

/**
 * 角色修改
 * @param {*} params
 */
export const roleUpdate = params => {
  return http.post(API_URL.ROLE_UPDATE_URL, Qs.stringify(params), {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  })
}

