/**
 * 菜单接口
 */
import http from '@/utils/http.js'
import { API_URL } from '@/utils/constants.js'
import Qs from 'qs'

/**
 * 菜单列表
 * @param {*} params  查询参数
 */
export const appParentModuleList = params => {
  return http.get(API_URL.APPPARENTMODULE_LIST_URL, { params })
}

/**
 * 菜单获取
 * @param {*} params
 */
export const appParentModule = params => {
  return http.get(API_URL.APPPARENTMODULE_URL + '/' + params.id, {})
}

export const appModule = params => {
  return http.get(API_URL.APPMODULE_URL + '/' + params.id, {})
}

export const menu = params => {
  return http.get(API_URL.MENU_URL + '/' + params.id, {})
}
/**
 * 菜单应用父模块新增
 * @param {*} params
 */
export const appParentModuleNew = params => {
  return http.post(API_URL.APPPARENTMODULE_NEW_URL, Qs.stringify(params), {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  })
}

