<template>
  <Card>
    <p slot="title">
      {{ title }}
    </p>
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="80"
      style="width: 500px"
    >
      <FormItem label="名称" prop="name">
        <Input v-model="formValidate.name" placeholder="请输入"></Input>
      </FormItem>
      <FormItem label="角色">
        <Select v-model="formValidate.role" placeholder="请选择">
          <Option v-for="item in roleList" :value="item.id" :key="item.id">{{
            item.description
          }}</Option>
        </Select>
      </FormItem>
      <FormItem label="文件" prop="wgtUrl">
        <div
          class="demo-upload-list"
          v-for="(item, index) in defaultList"
          :key="index"
        >
          <img :src="item.url" />
          <div class="demo-upload-list-cover">
            <Icon
              type="ios-eye-outline"
              @click.native="handleView(item)"
            ></Icon>
            <Icon
              type="ios-trash-outline"
              @click.native="handleRemove(item, index)"
            ></Icon>
          </div>
        </div>
        <Upload
          ref="upload"
          :show-upload-list="false"
          :default-file-list="defaultList"
          :action="upload_url"
          :data="fileData"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :on-progress="handleProgress"
          :on-error="handleError"
          :before-upload="onBefore"
          :on-success="handleSuccess"
          multiple
          type="drag"
          style="display: inline-block;width:58px;"
        >
          <div style="width: 58px;height:58px;line-height: 58px;">
            <Icon type="ios-camera" size="20"></Icon>
          </div>
        </Upload>
        <Modal title="图片" v-model="visible">
          <img :src="showUrl" v-if="visible" style="width: 100%" />
        </Modal>
      </FormItem>
      <FormItem label="排序" prop="rank">
        <Input v-model="formValidate.rank" placeholder="请输入"></Input>
      </FormItem>
      <FormItem label="版本" prop="version">
        <Input v-model="formValidate.version" placeholder="请输入"></Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formValidate')"
          >提交</Button
        >
        <Button style="margin-left: 8px" @click="back">返回</Button>
      </FormItem>
    </Form>
  </Card>
</template>
<script>
import { appModuleNew, appModuleUpdate } from '@/api/menu.js'
import { roleList } from '@/api/user.js'
import { SERVER_CODE, SERVER_URL } from '@/utils/constants.js'
export default {
  data() {
    return {
      fileData: { fileType: 1004, project: 'PORT', password: 'heihei' },
      defaultList: [],
      upload_url: SERVER_URL.UPLOAD_URL,
      showUrl: '',
      visible: false,
      title: '',
      roleList: [],
      formValidate: {
        name: '',
        parentModule: '',
        role: '',
        rank: '',
        appID: '1',
        wgtUrl: '',
        version: ''
      },
      ruleValidate: {
        name: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ],
        role: [
          {
            required: true,
            message: '请选择角色',
            trigger: 'change'
          }
        ],
        wgtUrl: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ],
        rank: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ],
        version: [
          {
            required: true,
            message: '不能为空',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  mounted() {},
  activated() {
    let data = JSON.parse(sessionStorage.getItem('menuInfo'))
    this.formValidate.parentModule = data.row.id
    this.defaultList = []
    this.type = this.$route.params.type
    if (this.type == 'add') {
      this.title = '添加子栏目'
      this.formValidate.parentModule = data.row.id
    } else {
      this.title = '修改子栏目'
      this.formValidate.parentModule = data.row.parentid
      this.formValidate.id = data.row.id
      this.formValidate.name = data.row.name
      this.formValidate.version = data.row.version
      this.formValidate.rank = data.row.rank
      let file = { name: 'file', url: data.row.wgtUrl }
      this.defaultList.push(file)
    }
    this.getRole()
  },
  methods: {
    handleSubmit(name) {
      debugger
      this.$refs[name].validate(valid => {
        if (valid) {
          switch (this.type) {
            case 'add':
              this.addChildren() //增加子菜单
              break
            case 'edit':
              this.editChildren() //编辑子菜单
              break
          }
        }
      })
    },
    getRole() {
      roleList({})
        .then(({ code, data, msg }) => {
          if (code === SERVER_CODE.CODE_200) {
            this.roleList = data
          } else {
            this.$Message.info(msg)
          }
        })
        .catch(error => {
          console.log(error)
        })
        .finally(() => {})
    },
    addChildren() {
      appModuleNew(this.formValidate)
        .then(({ code, msg }) => {
          if (code === SERVER_CODE.CODE_200) {
            this.$Message.success('子菜单新增成功！')
            this.$store.state.menuStatus = true
            setTimeout(() => {
              this.$router.go(-1)
            }, 2000)
          } else {
            this.$Message.info(msg)
            console.log('子菜单新增失败')
          }
        })
        .catch(error => {
          console.log(error)
        })
        .finally(() => {})
    },
    editChildren() {
      appModuleUpdate(this.formValidate)
        .then(({ code, msg }) => {
          if (code === SERVER_CODE.CODE_200) {
            this.$Message.success('子菜单编辑成功！')
            this.$store.state.menuStatus = true
            setTimeout(() => {
              this.$router.go(-1)
            }, 2000)
          } else {
            this.$Message.info(msg)
            console.log('子菜单编辑失败')
          }
        })
        .catch(error => {
          console.log(error)
        })
        .finally(() => {})
    },
    handleView(item) {
      this.showUrl = item.url
      this.visible = true
    },
    onBefore(file) {
      if (file.size > 102400 * 2000) {
        this.$Notice.warning({
          title: '文件大小错误',
          desc: file.name + '文件大小太大，超过200M'
        })
        return false
      }
      return true
    },
    handleError() {
      this.$Message.info('上传失败！')
    },
    handleRemove(item, index) {
      //删除文件
      console.log(item)
      console.log(index)
      this.defaultList = []
      this.formValidate.wgtUrl = ''
    },
    handleProgress() {
      //文件上传时的钩子
    },
    handlePreview(file) {
      //点击已上传的文件链接时的钩子
      location.href = file.url
    },
    handleSuccess(res, file) {
      //上传成功只保留一份文件
      this.defaultList = []
      let data = { name: file.name, url: res.data }
      this.defaultList.push(data)
      this.formValidate.wgtUrl = res.data
      this.$Message.success('上传成功！')
    },
    back() {
      this.$router.go(-1)
    }
  }
}
</script>
<style scoped>
.demo-upload-list {
  display: inline-block;
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  border: 1px solid transparent;
  border-radius: 4px;
  overflow: hidden;
  background: #fff;
  position: relative;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  margin-right: 4px;
}
.demo-upload-list img {
  width: 100%;
  height: 100%;
}
.demo-upload-list-cover {
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
}
.demo-upload-list:hover .demo-upload-list-cover {
  display: block;
}
.demo-upload-list-cover i {
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  margin: 0 2px;
}
</style>
